import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import homeImg from '../assets/img/home_dark.png'

function NavBar() {
    const toggleBTN = () => {
        const nav = document.querySelector('.mainHeader nav')
        nav.classList.toggle('active')
    }
    return (
        <>
            <div className="mainHeader">
                <nav >
                    <div className="toggle_btn">
                        <button id='toggleBTN' onClick={toggleBTN}>|||</button>
                    </div>
                    <ul>
                        <li>
                            <Link to="/">
                                <img src={homeImg} alt="" />
                            </Link>
                        </li>
                        <li className='orange'> 
                            <div className="subMenu">
                                <ul>
                                    <li><Link to="/history">  </Link></li>
                                    <li><Link to="/letsView">   </Link></li>
                                    <li><Link to="/letsView"> </Link></li>
                                    <li><Link to="/citigen"> </Link></li>
                                    <li><Link to="/">   </Link></li>
                                </ul>
                            </div>
                        </li>
                        <li className='red'><NavLink to="/notice"></NavLink></li>
                        <li className='green'><NavLink to="/member">   </NavLink></li>
                        <li className='tomato'><NavLink to="/kmkc">   </NavLink></li>
                        <li className='green'>
                            <div className="subMenu">
                                <ul>
                                    <li><Link to="#"> </Link></li>
                                    <li><Link to="#"> </Link></li>
                                    <li><Link to="#"> </Link></li>
                                </ul>
                            </div>
                        </li>
                        <li className='red'>
                            <div className="subMenu">
                                <ul>
                                    <li><Link to="/photo"> </Link></li>
                                    <li><Link to="/video"> </Link></li>
                                </ul>
                            </div>
                        </li>
                        <li className='slateblue'><Link to="/contact"></Link></li>
                        <li className='orange'>-
                            <div className="subMenu">
                                <ul>
                                    <li><Link to="/desital-center"> </Link></li>
                                </ul>
                            </div>
                        </li>
                        <li className='teal end'>
                            <div className="subMenu">
                                <ul>
                                    <li><Link to="#">  </Link></li>
                                    <li><Link to="#"></Link></li>
                                </ul>
                            </div>
                        </li>
                    </ul>
                </nav>
            </div>
        </>
    )
}

export default NavBar
